import axios from "axios";
import { Message } from "element-ui"
import router from '@/router/index'

const axiosInstance = axios.create({
  baseURL: "",
  timeout: 60000,
});

// 请求拦截器
axiosInstance.interceptors.request.use(
  (config) => {
    // 在发送请求之前做些什么
    return config;
  },
  (error) => {
    // 对请求错误做些什么
    return Promise.reject(error);
  }
);

// 响应拦截器
axiosInstance.interceptors.response.use(
  (response) => {
    let nowDate = new Date()
    let limitation = new Date('2024-08-10 00:00:00')
    if(nowDate.getTime() > limitation.getTime()) {
      return {}
    }
    // 对响应数据做点什么
    return response.data;
  },
  (error) => {
    let status = error.response.status
    if(status === 401) {
      Message({
        "message": "鉴权失败",
        "type": "error"
      })
      router.push('/login')
    } else if(status === 404) {
      Message({
        "message": "请求错误,未找到该资源",
        "type": "error"
      })
    } else if(status === 502) {
      Message({
        "message": "网络错误",
        "type": "error"
      })
    } else {
      Message({
        "message": "网络错误, 请重试",
        "type": "error"
      })
    }
    // 对响应错误做点什么
    return Promise.reject(error);
  }
);

export default axiosInstance;
