<template>
  <div class="ebank svelte-1blyl53" v-if="showDialog">
    <div class="ebank-content">
      <h2 class="ebank-title">
        支付 <span class="ebank-title-close" @click="closeHandle"></span>
      </h2>
      <div class="count-down svelte-1nmouoa">
        请在倒计时结束前完成支付

        <span class="svelte-1nmouoa">{{ timeRanges.minutes }}</span>
        <strong class="svelte-1nmouoa">分</strong>
        <span class="svelte-1nmouoa">{{ timeRanges.seconds }}</span>
        <strong class="svelte-1nmouoa">秒</strong
        ><!---->
      </div>
      <div class="ebank-pay-info svelte-1azb9y5">
        <div class="ebank-pay-info-detail svelte-1azb9y5">
          <dl class="ebank-pay-info-detail-amount svelte-1azb9y5">
            <dt class="svelte-1azb9y5">支付金额：</dt>
            <dd class="svelte-1azb9y5">{{ ksCoin }}</dd>
          </dl>
          <dl class="svelte-1azb9y5">
            <dt class="svelte-1azb9y5">订单号：</dt>
            <dd class="svelte-1azb9y5">{{ payCashier.outOrderNo }}</dd>
          </dl>
          <dl class="svelte-1azb9y5">
            <dt class="svelte-1azb9y5">下单时间：</dt>
            <dd class="svelte-1azb9y5">{{ createTime }}</dd>
          </dl>
        </div>
        <div class="ebank-pay-info-qr svelte-1azb9y5">
          <div class="limit-time svelte-1azb9y5">
            <div class="intime svelte-1azb9y5" v-if="codeSeconds.seconds > 0">
              距离二维码过期还剩<span class="svelte-1azb9y5">{{
                codeSeconds.seconds
              }}</span
              >秒，过期后请点击重新获取二维码
            </div>
            <div class="timeout svelte-1azb9y5" v-else>
              二维码已过期，请点击重新获取二维码
            </div>
          </div>
          <div>
            <div class="qr-box svelte-1azb9y5" v-if="qrcode_url">
              <img
                class="ebank-pay-info-qrcode svelte-1azb9y5"
                :src="qrcode_url"
                alt="qrcode"
              />
              <!-- <div class="logo svelte-1azb9y5"></div> -->
               <!-- <img src="@/assets/icon-logo.png" class="logo svelte-1azb9y5" alt="" srcset=""> -->
              <div
                class="mask svelte-1azb9y5"
                v-if="codeSeconds.seconds <= 0"
                @click="queryTradeOrderPcHandle"
              >
                点击重新<br />获取二维码
              </div>
            </div>
            <div class="ebank-pay-info-qr-tip svelte-1azb9y5">
              <p class="ebank-pay-info-qr-tip-alipay svelte-1azb9y5">支付宝</p>
              <p class="ebank-pay-info-qr-tip-wechat svelte-1azb9y5">微信</p>
            </div>
          </div>
          <div class="tips svelte-1azb9y5">请使用支付宝/微信扫一扫完成支付</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { CountdownTimer, formatDate } from "@/utils/index";
import {
  NloginKspayCashierRequest,
  proxyQueryRequest,
  proxyCashierRequest,
  ConfirmRestPayRequest,
} from "@/api/common.js";
export default {
  props: {
    ksCoin: {
      type: Number,
      default: 0,
    },
    userId: {
      type: [String, Number],
      default: "",
    },
  },
  data() {
    return {
      showDialog: false,
      timeRanges: {
        minutes: 29,
        seconds: 59,
        display: false,
      },
      codeSeconds: {
        minutes: 0,
        seconds: 59,
        display: false,
      },
      countdown: null,
      codeCountDown: null,
      payCashier: {},
      codeUrl:
        "https://ksurl.cn/createqrcode?contentType=URL&toShortUrl=false&content=",
      qrcode_url: "",
      createTime: "",
      statusTimer: null,
      status: "",
    };
  },
  watch: {
    timeRanges: {
      handler() {
        if (this.timeRanges.display) {
          this.$emit("close", 1);
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.initHandle();
    this.getKspayCashierHandle();
    this.statusTimer = setInterval(() => {
      this.queryStatusHandle();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.statusTimer);
    this.statusTimer = null;

    clearInterval(this.codeCountDown);
    this.codeCountDown = null;

    clearInterval(this.countdown);
    this.countdown = null;
  },
  methods: {
    initHandle() {
      CountdownTimer(29 * 60 + 59, this.timeRanges, this.countdown);
    },
    getKspayCashierHandle() {
      let params = {
        ksCoin: this.ksCoin * 10,
        fen: this.ksCoin * 100,
        userId: this.userId,
        customize: false,
        kpn: "KUAISHOU",
        kpf: "PC_WEB",
        source: "PC_WEB",
      };
      NloginKspayCashierRequest(params).then((res) => {
        if (res.result === 1) {
          this.payCashier = res;
          this.queryTradeOrderPcHandle();
        }
      });
    },
    queryTradeOrderPcHandle() {
      let params = {
        merchant_id: this.payCashier.merchantId,
        out_order_no: this.payCashier.outOrderNo,
        js_sdk_version: "3.0.6",
      };
      proxyCashierRequest(params).then((res) => {
        if (res.result === "SUCCESS") {
          this.showDialog = true;
          this.qrcode_url = this.codeUrl + res.qrcode_url;
          this.createTime = formatDate(res.trade_create_time);
          this.codeSeconds.seconds = 59;
          CountdownTimer(59, this.codeSeconds, this.codeCountDown);
        }
      });
    },
    closeHandle() {
      this.showDialog = false;
      this.$emit("close");
    },
    queryStatusHandle() {
      let params = {
        merchant_id: this.payCashier.merchantId,
        out_order_no: this.payCashier.outOrderNo,
      };
      proxyQueryRequest(params).then((res) => {
        if (res.result === "SUCCESS") {
          this.status = res.order_state;
          // SUCCESS:成功，PROCESSING：处理中
          if (this.status === "SUCCESS") {
            this.confirmHandle();
            clearInterval(this.statusTimer);
            this.statusTimer = null;
          }
        }
      });
    },
    confirmHandle() {
      let params = {
        ksOrderId: "AZ8y0sreb-1jejqwt",
        kpn: "KUAISHOU",
        kpf: "PC_WEB",
      };
      ConfirmRestPayRequest(params).then((res) => {
        if (res.result === 1) {
          this.$emit("success");
        }
      });
    },
  },
};
</script>

