<template>
  <div class="com-main">
    <div class="pay-wrap">
      <ul>
        <li>
          <div class="item-name">充值账号</div>
          <div class="fake-login" v-if="userInfo.userName && userInfo.userId">
            <div class="user-info">
              {{ userInfo.userName }} (快手ID: {{ userInfo.userId }})
            </div>
            <div class="checkout" @click="switchHandle">[切换账号]</div>
          </div>
          <div class="item-value item-value-flex" v-else>
            <div class="ks-number">
              <input
                type="text"
                placeholder="请输入快手号/快手ID"
                maxlength="50"
                v-model="userId"
                @focus="focusHandle"
                @blur="blurHandle"
              />
              <div class="confirm-btn" v-if="userId" @click="getUserInfoData">
                确认
              </div>
              <div class="error-tips" v-if="errorShow">
                您输入的快手号/快手ID不存在，请确认后重新输入。
              </div>
            </div>
            <!-- <span class="checkout-accout login-account" v-if="showSaoCode">
              <span>或</span>
              [扫码登录]
            </span> -->
          </div>
        </li>
        <li class="money-cell">
          <span class="item-name item-amount">充值金额</span>
          <div class="money-desc">
            <p class="money-desc-bi-default">充值比例：1 元 = 10 快币</p>
          </div>
          <div>
            <div class="recharge">
              <div
                class="recharge-gear payItem"
                v-for="(item, index) in rechargeGearList"
                :key="index"
                :class="{ 'pay-gear-checked': active === index }"
                @click="selectHandle(item, index)"
              >
                <template v-if="item.type === 1">
                  <span class="pay-yuan">{{ item.value }}元</span>
                  <span class="kscoin">{{ item.rate }}快币</span>
                </template>
                <template v-else-if="item.type === 2">
                  <template v-if="showInput && active === 5">
                    <span class="pay-yuan other-recharge-box">
                      <input
                        v-model.number="item.value"
                        id="myInput"
                        class="pay-money input-recharge"
                        name="other-recharge"
                        maxlength="8"
                        autofocus
                        onkeyup="value=value.replace(/^(0+)|[^\d]+/g,'')"
                        @change="changeValueHandle"
                        @focus="inputValueFocus"
                        @blur="inputValueBlur"
                      />
                      <span class="yuan input-recharge">元</span>
                    </span>
                    <span class="kscoin other-recharge-box">
                      <span class="money-desc-bi-more input-recharge">{{
                        Number(item.value) ? item.value * 10 : 0
                      }}</span>
                      <span class="kscoin input-recharge">快币</span>
                    </span>
                  </template>
                  <span
                    class="other-recharge-text"
                    v-else-if="!item.value && !showInput"
                    >其他金额
                  </span>
                  <template v-else-if="item.value && item.rate">
                    <span class="pay-yuan input-yuan com-input"
                      >{{ item.value }}元</span
                    >
                    <span class="kscoin input-kscoin com-input"
                      >{{ item.rate }}快币</span
                    >
                  </template>
                </template>
              </div>
            </div>
          </div>
        </li>
        <li>
          <div>
            <span class="go-button go-pay go-pay-checked" @click="goToPayHandle"
              >前往支付</span
            >
          </div>
        </li>
      </ul>
    </div>
    <dialog-index
      v-if="showDialog"
      @close="closeDialogHandle"
      :ksCoin="payData.value"
      :userId="userInfo.userId"
      @success="successHandle"
    ></dialog-index>
    <tip-component v-if="comTipShow" @close="closeTipHandle" :tipString="tipString"></tip-component>
    <success-dialog v-if="successShow" @close="successShow=false"></success-dialog>
  </div>
</template>

<script>
import { RestPayUserInfoRequest } from "@/api/common.js";
import dialogIndex from "./components/dialogIndex.vue";
import tipComponent from "./components/tipComponent.vue";
import successDialog from "./components/successDialog.vue";
export default {
  name: "homePage",
  components: {
    dialogIndex,
    tipComponent,
    successDialog
  },
  data() {
    return {
      successShow: false,
      comTipShow: false,
      showDialog: false,
      showSaoCode: false,
      showConfirmBtn: false,
      errorShow: false,
      showInput: false,
      userId: "",
      userInfo: {
        userId: "",
      },
      payCashier: {},
      payData: {
        value: 30,
        rate: 300,
      },
      rechargeGearList: [
        { value: 30, rate: 300, type: 1 },
        { value: 58, rate: 580, type: 1 },
        { value: 198, rate: 1980, type: 1 },
        { value: 688, rate: 6880, type: 1 },
        { value: 2688, rate: 26880, type: 1 },
        { value: null, rate: 0, type: 2 },
      ],
      active: 0,
      tipString: ''
    };
  },
  mounted() {
    // this.getUserInfoData();
  },
  methods: {
    successHandle(){
      this.successShow = true
      this.showDialog = false
    },
    focusHandle() {
      this.showSaoCode = false;
    },
    blurHandle() {
      this.showSaoCode = true;
    },
    inputValueFocus() {},
    inputValueBlur() {
      this.showInput = false;
    },
    changeValueHandle() {
      if(Number(this.rechargeGearList[5].value)) {
        this.rechargeGearList[5].rate = this.rechargeGearList[5].value * 10;
      } else {
        this.rechargeGearList[5].rate = 0
        this.rechargeGearList[5].value = 0
      }
      
    },
    switchHandle() {
      this.userInfo = {};
      this.userId = "";
    },
    selectHandle(item, index) {
      this.active = index;
      this.payData = item;
      if (this.active === 5) {
        this.showInput = true;
        this.$nextTick(() => {
          document.getElementById("myInput").focus();
        })
        
      }
    },
    goToPayHandle() {
      if (!this.errorShow && this.userInfo.userId && this.payData.value) {
        this.showDialog = true;
      } else {
        if(!this.userInfo.userId) {
          this.showTip('请填写用户快手号/快手ID')
        } else if(!this.payData.value) {
          this.showTip('至少充值1元')
        }
      }
    },
    getUserInfoData() {
      RestPayUserInfoRequest({ id: this.userId }).then((res) => {
        if (res.result === 1) {
          this.userInfo = res;
          this.errorShow = false;
        } else {
          this.errorShow = true;
        }
      });
    },
    closeTipHandle() {
      this.comTipShow = false;
    },
    closeDialogHandle(value) {
      this.showDialog = false;
      if(value === 1) {
        this.showTip('订单已超时')
      } else {
        this.showTip('用户手动关闭')
      }
      
    },
    showTip(string) {
      this.tipString = string
      this.comTipShow = true
    }
  },
};
</script>

<style lang="scss" scoped>
.com-main {
  width: 1100px;
  padding: 0 50px;
  margin: 0 auto;
}

.pay-wrap {
  margin-top: 50px;
  font-size: 14px;
  padding-left: 60px;
}

.pay-wrap ul {
  .item-name {
    display: block;
    color: #5e5e5e;
    height: 22px;
    line-height: 22px;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 32px;
  }

  .item-value-flex {
    display: flex;
    align-items: center;
  }

  .item-value {
    line-height: 22px;
    // display: inline-block;
  }

  .ks-number {
    position: relative;
  }

  .ks-number input {
    padding-left: 12px;
    padding-right: 100px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 350px;
    height: 54px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    font-family: PingFangSC, PingFangSC-Medium;
    font-weight: 500;
    color: #131415;
  }

  .confirm-btn {
    user-select: none;
    // display: none;
    position: absolute;
    width: 74px;
    height: 32px;
    border-radius: 18px;
    font-size: 16px;
    font-family: PingFangSC, PingFangSC-Medium;
    font-weight: 500;
    text-align: center;
    line-height: 32px;
    cursor: pointer;
    top: 50%;
    right: 12px;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    background-color: #ed5d2a;
    color: #f9eeea;
  }

  .ks-number .error-tips {
    // display: none;
    position: absolute;
    height: 20px;
    font-size: 14px;
    font-family: PingFangSC, PingFangSC-Regular;
    font-weight: 400;
    color: #ff5000;
    line-height: 20px;
    top: calc(100% + 4px);
    left: 0;
  }

  .checkout-accout {
    margin-left: 16px;
    color: #006fe1;
    font-weight: 500;
    cursor: pointer;
  }

  .checkout-accout span {
    color: #d5d7d8;
    cursor: default;
  }

  .fake-login {
    display: flex;
    height: 54px;
    align-items: center;
  }

  .fake-login .user-info {
    font-size: 16px;
    font-family: PingFangSC, PingFangSC-Medium;
    font-weight: 500;
    line-height: 22px;
  }

  .fake-login .checkout {
    margin-left: 16px;
    color: #006fe1;
    cursor: pointer;
  }

  .item-amount {
    margin-top: 60px;
    margin-bottom: 4px;
  }
}

.recharge {
  width: 700px;
  font-size: 0;
}

.recharge-gear {
  text-align: center;
  display: inline-block;
  width: 190px;
  height: 80px;
  margin: 0 24px 24px 0;
  line-height: 50px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #ccc;
  vertical-align: top;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
}

.recharge-gear .pay-yuan {
  height: 29px;
  color: #131415;
  font-size: 20px;
  display: block;
}

.recharge-gear .kscoin {
  height: 22px;
  color: #969798;
  font-size: 16px;
  font-weight: 400;
}

.pay-gear-checked {
  border: 2px solid #ff5000;
  position: relative;
}
.recharge-gear .other-recharge-text {
  font-size: 18px;
  line-height: 75px;
  color: #969798;
}
.recharge-gear .pay-yuan {
  height: 29px;
  color: #131415;
  font-size: 20px;
  display: block;
}
.pay-money {
  display: inline-block;
  width: 80px;
  height: 24px;
  border-bottom: 1px solid rgba(150, 151, 152, 0.5);
  font-size: 16px;
}
.go-button {
  font-size: 16px;
  display: inline-block;
  width: 176px;
  height: 48px;
  background: #ccc;
  text-align: center;
  line-height: 48px;
  color: #fff;
  border-radius: 24px;
  margin-top: 24px;
  cursor: pointer;
}
.go-button.go-pay-checked {
  background: #ff5000;
}
.money-desc {
  display: inline-block;
  width: 200px;
  vertical-align: middle;
  font-size: 14px;
  margin-bottom: 24px;
}
</style>