import axiosInstance from "@/utils/request";
import { apiPrefix } from "./index";

export function RestPayUserInfoRequest(data) {
  return axiosInstance.post(apiPrefix + "/rest/k/pay/userInfo", data);
}

export function ConfirmRestPayRequest(data) {
  return axiosInstance.post(apiPrefix + "/rest/k/pay/kscoin/deposit/nlogin/kspay/confirm?kpn=KUAISHOW&kpf=PC_WEB", data);
}

export function PayOrderPcTradeQueryRequest(data) {
  let formData = new FormData()
  Object.keys(data).forEach(key => {
    formData.append(key, data[key])
  }) 
  return axiosInstance.post(apiPrefix + "/pay/order/pc/trade/query", formData);
}

export function NloginKspayCashierRequest(data) {
  return axiosInstance.post(apiPrefix + "/rest/k/pay/kscoin/deposit/nlogin/kspay/cashier", data);
}

export function PayOrderPcTradeCashierRequest(data) {
  let formData = new FormData()
  Object.keys(data).forEach(key => {
    formData.append(key, data[key])
  }) 
  return axiosInstance.post(apiPrefix + "/pay/order/pc/trade/cashier", formData);
}

export function proxyCashierRequest(data) {
  let formData = new FormData()
  Object.keys(data).forEach(key => {
    formData.append(key, data[key])
  }) 
  let url = 'https://pay.ssl.kuaishou.com/pay/order/pc/trade/cashier'
  return axiosInstance.post(apiPrefix + "/proxy?url=" + url, formData);
}

export function proxyQueryRequest(data) {
  let formData = new FormData()
  Object.keys(data).forEach(key => {
    formData.append(key, data[key])
  }) 
  let url = 'https://pay.ssl.kuaishou.com/pay/order/pc/trade/query'
  return axiosInstance.post(apiPrefix + "/proxy?url=" + url, formData);
}

